.container {
  padding: 1.6rem;
  text-align: center;
  display: grid;
  grid-template-areas: "header header" "nav main" "nav footer";
  grid-template-columns: min-content auto;
  grid-gap: 1.6rem;
}
@media (min-width: 52em) {
  .container {
    display: block;
  }
}

.header {
  grid-area: header;
}

.nav {
  grid-area: nav;
}

.main {
  grid-area: main;
}

.footer {
  grid-area: footer;
}

.title {
  font-size: 2em;
}

.navList {
  display: flex;
  flex-direction: column;
  height: 100%;
  counter-reset: section;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 52em) {
  .navList {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 50em;
  }
}

.navItem {
  margin: 0 0 1em;
}
@media (min-width: 52em) {
  .navItem {
    flex: 1;
    margin: 0 0.3em 0.3em;
  }
}

.navSection {
  composes: navItem;
}

.styledButton {
  color: inherit;
  background-color: transparent;
  border: 1px solid currentColor;
  padding: 0.35em 1.5em;
  border-radius: 0.9em;
  cursor: pointer;
  font: inherit;
  display: block;
  text-transform: capitalize;
  user-select: none;
  white-space: nowrap;
}
.styledButton.active, .styledButton.prev {
  color: #3abdd9;
}
.styledButton.active {
  cursor: default;
  background-color: #e2e2e2;
}
.styledButton:not(.active, .buttonSubmit):hover {
  background-color: #e2e2e2;
}

.navItemButton {
  width: 100%;
  counter-increment: section;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0.5em;
  text-align: left;
}
.navItemButton:before {
  content: counter(section);
  font-size: 0.8em;
  height: 1.4em;
  width: 1.4em;
  background-color: #0d131e;
  color: #fff;
  text-align: center;
  line-height: 1.4em;
  border-radius: 50%;
  margin-right: 0.5em;
  font-weight: bold;
}
.navItemButton.active {
  background-color: #3abdd9;
  color: #fbfbfb;
}

.navSectionButton {
  composes: styledButton;
  width: 100%;
  font-weight: bold;
}

.main {
  padding: 2em min(1.6rem, 3vw);
  text-align: initial;
  width: min(100%, 50em);
  border: 1px solid #ccc;
  margin: 0 auto;
  border-radius: 1em;
}
@media (min-width: 52em) {
  .main {
    margin: 1.5em auto;
  }
}

.question {
  margin-top: 0;
  font-size: 1.5em;
}

.formGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.radioLabel {
  flex: 1;
  margin: 0 0.3em 0.3em;
}
.radioLabel input[type=radio] {
  display: none;
}
.radioLabel span {
  display: block;
  font-size: 1.2em;
  padding: 0 1.2em;
  line-height: 2.1;
  border: 1px solid #afafaf;
  border-radius: 0.5em;
  cursor: pointer;
  text-align: center;
  user-select: none;
}
.radioLabel input[type=radio]:checked + span {
  border-color: #3abdd9;
}

.controlInput {
  display: block;
  width: 100%;
  font-size: 1.2em;
  line-height: 2;
  padding-left: 1.2em;
  border: 1px solid #afafaf;
  border-radius: 0.5em;
  margin-bottom: 0.3em;
}
.controlInput:hover, .controlInput:focus {
  outline-width: 0.1rem;
  outline-color: #3abdd9;
}

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer span {
  margin-right: 0.6em;
  display: flex;
  align-items: center;
  white-space: nowrap;
}
.footer b {
  margin-left: 0.3em;
}
.footer svg {
  margin-right: 0.3em;
}

.footerButton {
  font-size: 1.4em;
  border-radius: 0.2em;
  padding: 0.4em 2em;
  margin-top: 0.3em;
  margin-bottom: 0.3em;
}

.buttonNext {
  composes: styledButton;
}

.buttonSubmit {
  composes: styledButton;
  composes: footerButton;
  background-color: #3abdd9;
  color: #fbfbfb;
}
.buttonSubmit:hover {
  background-color: #24a1bc;
}